.pagination {
  padding: 0;
  list-style: none;
  display: flex;
}
.pagination__item {
  margin-left: 5px;
  margin-right: 5px;
}
.pagination__button {
  background: @grey;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  padding: 0;
  border: none;
  display: block;
  &--active {
    background: @blue;
    box-shadow: 0px 0px 10px rgba(78, 138, 255, 0.5);
  }
}
