.features {
  padding: 0;
  list-style: none;
}
.features__item {
  width: 246px;
  margin: 0 auto;
  padding: 73px 50px 70px 50px;
  min-height: 295px;
  background-image: url("../img/feature-bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
.features__img-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  background-color: @blue;
  line-height: 50px;
}
.features__img {
  vertical-align: middle;
}
.features__title {
  font-weight: 900;
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 15px;
}
.features__text {
  color: @dark-grey;
  margin: 0;
}
