.menu {
  position: absolute;
  min-height: 852px;
  background: @blue;
  top: 0;
  width: 320px;
  padding: 30px 20px;
}
.menu__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 152px;
}
.menu__close-button {
  width: 45px;
  height: 45px;
  background-color: transparent;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
}
.menu__navigation {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  margin-bottom: 240px;
}
.menu__item {
  margin-bottom: 60px;
  &:last-child {
    margin-bottom: 0;
  }
}
.menu__link {
  font-size: 24px;
  font-weight: bold;
  color: @white;
  text-decoration: none;
}
.menu__contacts {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center;
  color: @white;
  font-weight: 500;
}
.menu__contacts-item {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.menu__contacts-icon {
  vertical-align: middle;
}
.menu__contacts-phone {
  font: inherit;
  text-decoration: none;
  color: inherit;
}
