.photo {
  padding: 20px;
}
.photo__wrapper {
  position: relative;
  margin-bottom: 15px;
  &::before {
    content: "";
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: -20px;
    right: -20px;
    background: #4E8AFF;
    opacity: 0.4;
    border-top-left-radius: 15px;
    z-index: -1;
  }
}
.photo__img {
  border-radius: 15px;
}
.photo__title {
  font-weight: bold;
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 10px;
  text-align: center;
}
.photo__text {
  margin-top: 0;
  margin-bottom: 0;
  color: @dark-grey;
  text-align: center;
}
