.slider {
  background-color: #F7F8FF;
  padding: 50px 20px 45px 20px;
}
.slider__slides {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 20px;
}
.slider__button {
  width: 45px;
  height: 45px;
  background: #4E8AFF;
  box-shadow: 0px 10px 25px rgba(78, 138, 255, 0.25);
  border: none;
  border-radius: 50%;
  flex-shrink: 0;
  background-repeat: no-repeat;
  background-position: center;
  &--prev {
    background-image: url("../img/icon-chevron-left.svg");
  }
  &--next {
    background-image: url("../img/icon-chevron-right.svg");
  }
}
.slider__controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.slider__pagination {
  margin: 0;
}
