.card-service {
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
  border-radius: 25px;
  overflow: hidden;
}
.card-service__img {
  order: -1;
  margin-bottom: 20px;
}
.card-sevice__title {
  margin-top: 0;
  margin-bottom: 15px;
  text-align: center;
  font-weight: bold;
  font-size: inherit;
  padding: 0 5px;
}
.card-service__text {
  padding: 0 15px;
  text-align: center;
  font-size: 15px;
  color: @dark-grey;
  margin-bottom: 0;
}
.card-service__button {
  margin: 30px 16px;
  width: calc(100% - 32px);
  padding-top: 14px;
  padding-bottom: 14px;
  & img {
    margin-left: 12px;
  }
}
