.page-header {
  padding: 28px 20px;
  display: flex;
  justify-content: space-between;
  background-color: @white;
  border-bottom: 1px solid @blue;
}

.page-header__menu {
  right: 0;
  display: none;
}

.page-header__open-menu {
  display: block;
  width: 45px;
  height: 45px;
  border: none;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg width='35' height='23' viewBox='0 0 35 23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M34.375 22.75H11.875V19H34.375V22.75ZM34.375 13.375H0.625V9.625H34.375V13.375ZM34.375 4H11.875V0.25H34.375V4Z' fill='%23353B49'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center;
}
.page-header__open-menu[aria-expanded="true"] + .page-header__menu {
  display: block;
}
