.lang-toggler {
  padding: 0;
  list-style: none;
  display: inline-flex;
  justify-content: center;
  background-color: @grey;
  border-radius: 10px;
  overflow: hidden;
}
.lang-toggler__link {
  display: block;
  padding: 5px 13px;
  color: @white;
  font-weight: bold;
  font-size: 15px;
  text-decoration: none;
  &:not([href]) {
    background-color: @dark;
    border-radius: 10px;
  }
}
