.section{
  margin-bottom: 100px;
  &--about {
    display: flex;
    flex-direction: column;
  }
  &--form {
    margin-bottom: 50px;
  }
  &--contacts {
    margin-bottom: 30px;
  }
}
.section__photo {
  order: -1;
  margin: 0;
  margin-bottom: 50px;
}
.section__title {
  padding: 0 20px;
  font-weight: 800;
  font-size: 36px;
  color: @blue;
  margin-top: 0;
  margin-bottom: 20px;
  &--small {
    font-size: 29px;
  }
}
.section__subtitle {
  padding: 0 20px;
  font-weight: bold;
  font-size: 20px;
  line-height: 1.25;
  margin-top: 0;
  margin-bottom: 20px;
}
.section__text {
  padding: 0 20px;
  line-height: 1.4;
  & a {
    color: @blue;
  }
}
.section__services {
  margin-top: 60px;
  list-style: none;
}
.section__slider {
  margin-top: 50px;
}
.section__form {
  margin-top: 50px;
}
.section__contacts {
  margin-left: 20px;
}
