.page-footer {
  padding-top: 135px;
  padding-bottom: 30px;
  position: relative;
  background-color: @blue;
  text-align: center;
  &::before {
    content: "";
    display: block;
    top: 0;
    left: 0;
    right: 0;
    height: 157px;
    position: absolute;
    background-color: @white;
    background-image: url("../img/footer--mobile-bg.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}
.page-footer__content {
  position: relative;
}
.page-footer__logo {
  margin-bottom: 30px;
}
.page-footer__copy {
  color: @white;
  margin-bottom: 70px;
}
.page-footer__menu {
  margin: 0;
  margin-bottom: 75px;
}
.page-footer_lang-toggler {
  margin: 0;
}
