.contacts {
  padding: 40px 21px 65px 21px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(78, 138, 255, 0.1);
  border-radius: 25px 0px 0px 25px;
  margin-bottom: 30px;
}
.contacts__logo {
  margin-bottom: 60px;
}
.contacts__items {
  margin: 0;
  padding: 0;
  list-style: none;
}
.contacts__item {
  font-weight: 500;
  padding-left: 24px;
  position: relative;
  margin-bottom: 23px;
  &--address{
    margin-bottom: 60px;
  }
  &:last-child {
    margin-bottom: 0;
  }
}
.contacts__icon {
  position: absolute;
  top: 50%;
  bottom: 50%;
  left: 0;
  transform: translateY(-50%);
}
.contacts__phone {
  color: inherit;
  text-decoration: none;
  padding-left: 5px;
}
